import { Component, OnInit } from '@angular/core';
import { NavController } from '@ionic/angular';

@Component({
  selector: 'app-points-success-msg',
  templateUrl: './points-success-msg.page.html',
  styleUrls: ['./points-success-msg.page.scss'],
})
export class PointsSuccessMsgPage implements OnInit {
  transferMobileNo: string;
  pointsGet: string;

  constructor(private navCtrl:NavController) { 
   this.transferMobileNo = localStorage.getItem('transferMobNo');
   this.pointsGet = localStorage.getItem('totalPoints');
  }

  ngOnInit() {
  }

  goToHome(){
    this.navCtrl.navigateForward('point-transfer')
  }
}
